@tailwind base;
@tailwind components;
@tailwind utilities;
// Variables
$primary-color: #0073aa;
$border-color: #ddd;
$text-color: #333;
$text-secondary: #666;
$white: #fff;
$transition-speed: 0.2s;

// Mixins
@mixin card-shadow {
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

@mixin flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.page-template-region-select-template {
    #wrapper {
        #main {
            // Main container
            .lang-select {
                cursor: pointer;
                &:hover {
                    box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.175);
                    border-radius: 0.5rem;
                }
            }
        }
    }
}
/**
Remove Menu From Main Navigation
**/
.fusion-secondary-header {
    display: none;
}
nav {
    &.fusion-main-menu {
        display: none;
    }
}